html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, Roboto, 'Segoe UI', 'Segoe WP', 'Helvetica Neue', 'Lucida Grande', Verdana, Tahoma, Arial, sans-serif;
  font-size: 16px;
  color: #353b3f;
}

* {
  margin: 0; padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, Roboto, 'Segoe UI', 'Segoe WP', 'Helvetica Neue', 'Lucida Grande', Verdana, Tahoma, Arial, sans-serif !important;
  color: #353B3F;
}

.wrapper {
  width: 100vw;
  height: 100vh;  
  display: grid;
  grid-template-columns: 27% 73%;
  grid-template-rows: 9% 91%;
  /* border-top: 12px solid #A9649E; */
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.bContent {
  padding: 0 25px;
}

.bContent h1 {
  font-weight: 500;
  font-size: 2.25rem;
  line-height: 1;
  border-bottom: 1px solid #eee;
  padding-bottom: 10px;
}

.bContent h3 {
  padding: 2rem 0;
  padding-bottom: 10px;
  font-weight: 400;
  font-size: 1.25rem;
  color: #989898;
  line-height: 1;
  border-bottom: 1px solid #eee;
  margin-bottom: 1rem;
}

.innerContent {
  padding: 1rem 0;
}

.bDescription {
  font-size: 15px;
  line-height: 1.5;
  color: #989898;
  max-width: 800px;
  font-weight: 300;
  padding-bottom: 1rem;
}


